* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

li {
  padding: 0;
  margin: 0;
  list-style: none;
}

.country {
  display: flex;
  align-items: center;
  font-size: 24px;
  /* font-weight: 600; */
}
.country.selected {
  font-weight: 700;
}

.country:not(:last-child){
  margin-bottom: 10px;
}

.country>img {
  margin-right: 10px;
  width: 32px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 0px 3px;
}

.country-info-item:not(:last-child) {
  margin-bottom: 10px;
}

.country-info-item>span {
  font-weight: 600;
  margin-right: 6px;
}